.App {
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(#054164, #152238);
    display: flex;
    flex-direction: column;
    color: white;
}

.App-links {
    display: flex;
    justify-content: flex-end;
    column-gap: 3vmin;
    padding: 3vmin;
    font-size: 3vmin;
}

.link {
    color: white;
    text-decoration: none;
    transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.linkedin-link {
    &:hover {
        color: #0077b5;
    }
}

.github-link {
    &:hover {
        color: #6cc644;
    }
}

.email-link {
    &:hover {
        color: #ffca0a;
    }
}

.App-intro {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 6vmin;
}

.App-name {
    font-weight: 500;
    font-size: 8vmin;
}
.App-subtitle {
    font-size: 3vmin;
}
